.top {
  overflow-x: hidden;
}

.top main {
  margin-top: 0;
}

.top #gu-header-js {
  position: absolute;
  width: 100%;
  z-index: 20;
}

.top .gu-header {
  background: transparent;
}

.top .gu-footer:before {
  content: "";
  position: absolute;
  background: url("/assets/images/wave_light01.png") no-repeat 0 0/100%;
  width: 100%;
  height: 7.81vw;
  top: -7.29vw;
}
@media screen and (max-width: 767px) {
  .top .gu-footer:before {
    height: 8.91vw;
    top: -7.38vw;
  }
}

.top .gu-section::before {
  content: none;
}

.top .gu-container {
  display: flex;
  justify-content: space-between;
  padding: 7.29vw 0;
}
@media screen and (max-width: 767px) {
  .top .gu-container {
    flex-direction: column;
    padding: 18.32vw 0 0;
  }
}

.gu-top-content {
  width: 50%;
}
@media screen and (max-width: 767px) {
  .gu-top-content {
    width: 100%;
    margin-bottom: 16.28vw;
  }
}

.top .gu-button-1 {
  margin-top: 3.28vw;
}
@media screen and (max-width: 767px) {
  .top .gu-button-1 {
    margin-top: 5.85vw;
    width: 28.5vw;
    font-size: 3.05vw;
    padding: 1.02vw 3.05vw;
    line-height: 1.5;
  }
}

.gu-top-inner {
  padding-left: 2.08vw;
}

.gu-top-contentImage {
  width: 100vw;
  max-width: 50%;
  margin: 0 calc(50% - 50vw) 0;
}
@media screen and (max-width: 767px) {
  .gu-top-contentImage {
    max-width: inherit;
  }
}

@media screen and (min-width: 768px) {
  .gu-section:nth-of-type(odd) .gu-top-contentImage {
    order: -1;
  }
}
.gu-top-contentImage img {
  width: 41.67vw;
  display: block;
}
@media screen and (max-width: 767px) {
  .gu-top-contentImage img {
    width: 100%;
  }
}

.gu-heading-3 {
  margin-top: 1em;
}
.gu-heading-3:first-of-type {
  margin-top: 0;
}

@media screen and (min-width: 768px) {
